import React, { Component } from "react";
import { Link } from "react-router-dom";
import Input from "./Input";
import Button from './Button';

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = { value: "Henry the III of England" };
    this.changeHandlerText = this.changeHandlerText.bind(this);
    this.clickHandler = this.clickHandler.bind(this);
  }
  changeHandlerText(event) {
    this.setState({ value: event.target.value });
  }
  clickHandler(event) {
      alert('hej')
  }
  render() {
      console.log(this.state)
    return (
      <div className="Main-page">
        <h2>Main page</h2>
        {/* <p>
          En homesida med ett inputfält man kan skriva i som routas till “/” och
          en Link till Person.
        </p> */}
        <div>
        <Input
          type="text"
          name="TextInput"
          value={this.state.value}
          changeHandler={this.changeHandlerText}
          />
          <Button className="Button" clickHandler={this.clickHandler} />
          <Link id="linkBtn" to={"/Person/"+this.state.value} name={this.state.value}>{this.state.value}</Link>
          </div>
      </div>
    );
  }
}

export default Main;
